import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import parser from "@/components/jyf-parser/jyf-parser";
import util from "../../static/js/util.js"
export default {
	name: "",
	components: {
		uniSearchBar,
		"jyf-parser": parser,
	},
	props: {},
	data() {
		return {
			initialVal: "",
			keyword: "",
			hotKeywordList: [],
			historyData: [],
			num: 0,
			searchResult: [],
			util: util.keyWord
		}
	},
	methods: {
		// 搜索框输入
		input(e) {
			this.num++
			if (this.keyword === e.value && this.num === 1) {
				return
			}
			this.keyword = e.value
			if (this.num <= 1) {
				return
			}
			let val = e.value.trim()
			if (val.length > 0) {
				this.$api.getSearch(val).then(res => {
					if (res.status === 200) {
						if (res.data.keywords.length === 0) {
							uni.showToast({
								title: "暂无相关数据",
								icon: "none"
							})
						} else {
							this.searchResult = res.data.keywords
							this.addHistory(val)
						}
					}
				}).catch(err => {
					console.log(err)
				})
			} else {
				this.searchResult = []
			}
		},
		// 添加搜索
		addSearch(e) {
			this.initialVal = e
			setTimeout(()=>{
				this.initialVal=""
			},1000)
		},
		// 跳转到详情页
		details(id) {
			uni.navigateTo({
				url: `/pages/details/details?id=${id}`
			});
		},
		// 删除搜索历史
		delHistory() {
			let _this=this
			uni.showModal({
				title: '提醒',
				content: '是否确认删除搜索历史？',
				success: function(res) {
					if (res.confirm) {
						_this.historyData=[]
						_this.$api.clearHistory({openId:_this.$store.state.openId}).then(res=>{
							if(res.data.data==="清除成功"){
								uni.showToast({
									title:"删除成功"
								})
							}
						}).catch(err=>{
							console.log(err)
						})
					} else if (res.cancel) {
						
					}
				}
			});
		},
		// 添加搜索历史
		addHistory(e){
			let flage=this.historyData.findIndex(item=>item.keyword===e)
			if(flage==-1){
				this.historyData.unshift({
					keyword:e
				})
				let a={keyword:e,openId:this.$store.state.openId}
				this.$api.addHistory(a).then(res=>{
					console.log(res)
				}).catch(err=>{
					console.log(err)
				})
			}
			
		},
		// 返回
		back() {

			uni.navigateBack({
				delta: 1
			});






		},
		getIndexaction() {
			this.$api.getIndexaction(this.$store.state.openId).then(res => {
				if (res.status === 200) {
					this.$store.state.indexaction = res.data
					this.initialVal = res.data.defaultKeyword.keyword
					this.hotKeywordList = res.data.hotKeywordList
					this.historyData = res.data.historyData
				
				}
			}).catch(err => {
				console.log(err)
			})
		}
	},
	mounted() {

	},
	onLoad() {
		let a = this.$store.state.indexaction
		this.getIndexaction()
	},
	filters: {

	},
	computed: {

	},
	watch: {
	},
	directives: {}
}
